<template>
  <div class="home debug">
    <QUX :app="hash" :config="config" :showDebug="true" v-model="viewModel"/>
  </div>
</template>
<style lang="scss">
  .home {
    height: 100%;
  }
</style>

<script>

import Vue from "vue";
import QUX from 'vue-low-code'
Vue.use(QUX);

export default {
  name: 'home',
  data: function () {
    return {
      hash: "a2aa10andClfVQD1NQEnygakNH21usGo4WF7gOTxhVou2h1zzlr9rN4DoK7C",
      viewModel: {
        selectedProducts : { // productid to quantity

        },
        selectedDiscounts: { // disocuntid to product in offer id

        },
        selectedProductsInNewStore: []
      },
      config: {
        debug: {
          resize: false,
          logLevel: 10
        },
        css: {
          grid: true,
          justifyContentInWrapper: true
        },
        router: {
          key: 'screenName',
          prefix: ''
        }
      },
      showPayload: false
    }
  },
  components: {
  },
  methods: {
    updateProductChoise() {
      this.viewModel.selectedProducts[this.viewModel.selectedProductInStore.id] = this.viewModel.productQuantity
    },
    confirmDiscountAllOrNothing() {
      // check if the chosen offers for the discount list is initialized already or not
      if(!this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name].length
          || !(this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name] instanceof Array)) {
        this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name] = []
      }
      this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name].push(this.viewModel.selectedOffer.productId)
    },
    confirmDiscountOneOf() {
      // check if the chosen offers for the discount list is initialized already or not
       if(!this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name].length
          || !(this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name] instanceof Array)) {
        this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name] = []
      }
      this.viewModel.selectedDiscounts[this.viewModel.selectedDiscount.name].push('ALL');
    }, 
    clearOrderAndDiscounts() {
      this.viewModel.selectedDiscounts = {}
      this.viewModel.selectedProducts = {}
    },
    addProductToNewStore() {
      if(!this.viewModel.selectedProductsInNewStore.map(selected => selected.id).includes(this.viewModel.chosenNewStoreProduct.id)) {
        this.viewModel.selectedProductsInNewStore.push({
          description: this.viewModel.chosenNewStoreProduct.description,
          id: this.viewModel.chosenNewStoreProduct.id,
          price: 0
        })
      }
    },
    sumOffers() {
      this.viewModel.sumOfOffersAdditionalCost = 
      this.viewModel.selectedDiscount.offers
      .map(offer => offer.additionalCost)
      .reduce((a, b) => a + b, 0)
    },
  }
}
</script>
